import { useLang, useLangAria } from '@app/services/config/hooks'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { responsive, spacing, CloseIcon } from '@instacart/ids-core'
// Button on IDS is not the same as cocktail, use this for now
import { Button, Text } from '@instacart/cocktail'
import AssetImage from '../../common/AssetImage'

export type ModalProps = {
  onContinue?: () => void
  onClose: () => void
}

const useStyles = createStyles(theme => ({
  brandLogo: {
    height: `${spacing.s48}px`,
  },
  container: {
    height: '100%',
    margin: '4.5vh auto',
    width: '620px',
    [responsive.only('c')]: {
      margin: 0,
      width: 'auto',
    },
  },
  contentCTA: {
    button: {
      marginBottom: `${spacing.s12}px`,
      maxWidth: '340px',
    },
    margin: `${spacing.s32}px 0`,
    textAlign: 'center',
    [responsive.only('c')]: {
      margin: `${spacing.s16}px 0`,
    },
  },
  contentLegal: {
    ...theme.typography.bodySmall2,
    marginTop: `${spacing.s24}px`,
  },
  contentTitle: {
    h3: theme.typography.titleMedium,
    margin: `0 ${spacing.s32}px`,
    span: theme.typography.bodyMedium2,
    textAlign: 'center',
  },
  modal: {
    WebkitOverflowScrolling: 'touch',
    // CC is the alpha
    backgroundColor: `${theme.colors.systemGrayscale70}CC`,
    bottom: 0,
    left: 0,
    outline: 'none',
    overflow: 'hidden',
    position: 'fixed',
    right: 0,
    top: 0,
    // .add-custom-item icon have a zIndex of 2000, have to use 2001 for now to place it ontop
    zIndex: 2001,
  },
  modalBanner: {
    backgroundColor: theme.colors.systemGrayscale10,
    height: '200px',
    img: {
      height: '100%',
      objectFit: 'contain',
      width: '100%',
    },
    padding: `${spacing.s32}px`,
    [responsive.only('c')]: {
      height: '150px',
    },
  },
  modalClose: {
    padding: 0,
    position: 'absolute',
    right: 0,
    top: `-${spacing.s24}px`,
    [responsive.only('c')]: {
      right: `${spacing.s16}px`,
      svg: {
        fontSize: spacing.s32,
      },
      top: 'unset',
    },
  },
  modalContent: {
    alignItems: 'center',
    background: theme.colors.systemGrayscale00,
    display: 'flex',
    flexDirection: 'column',
    padding: `${spacing.s12}px ${spacing.s16}px`,
    [responsive.only('c')]: {
      height: '100%',
    },
  },
  modalHeader: {
    alignItems: 'center',
    background: theme.colors.brandPrimaryRegular,
    display: 'flex',
    justifyContent: 'center',
    padding: `${spacing.s4}px 0`,
    position: 'relative',
  },
}))

const RxUpsellModal = ({ onClose, onContinue }: ModalProps) => {
  const styles = useStyles()
  const lang = useLang()
  const langAria = useLangAria()

  return (
    <div css={styles.modal}>
      <div css={styles.container}>
        <div css={styles.modalHeader}>
          <AssetImage css={styles.brandLogo} src={'images/header-logo.svg'} alt={lang.clientName} />
          <button css={styles.modalClose} onClick={() => onClose()}>
            <CloseIcon aria-label={langAria.modalClose} color="systemGrayscale00" />
          </button>
        </div>
        <div css={styles.modalBanner}>
          <AssetImage src={'images/rx-upsell-image.png'} alt={lang.clientName} />
        </div>
        <div css={styles.modalContent}>
          <div css={styles.contentTitle}>
            <Text component="h3" variant="large">
              {lang.rxDialog.title}
            </Text>
            <Text component="span" variant="body">
              {lang.rxDialog.subtitle}
            </Text>
          </div>
          {lang.rxDialog.legal && <span css={styles.contentLegal}>{lang.rxDialog.legal}</span>}
          <div css={styles.contentCTA}>
            <Button fullWidth variant="filled" onClick={() => onClose()}>
              {lang.rxDialog.addGroceries}
            </Button>
            {onContinue && (
              <Button fullWidth variant="outline" onClick={() => onContinue()}>
                {lang.rxDialog.continueCheckout}
              </Button>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default RxUpsellModal
